/**
 * bytes
 * @param fileSize String2MB230KB1TB
 * @param thousand boolean10001024
 */
let fileSize2Bytes = (fileSize, thousand) => {
  if (!fileSize) return null;
  let baseUnit = thousand ? 1000 : 1024;
  let kb = baseUnit,
    mb = kb * baseUnit,
    gb = mb * baseUnit;
  let tmpCode = fileSize.substring(fileSize.length - 2).toUpperCase();
  let sizeNumber = fileSize.substring(0, fileSize.length - 2);
  let num = Number.parseInt(sizeNumber);
  let result = 0;
  switch (tmpCode) {
    case "KB":
      result = num * kb;
      break;
    case "MB":
      result = num * mb;
      break;
    case "GB":
      result = num * gb;
      break;
  }
  return result;
};

